.stories {
        padding-block: remy(156) remy(80);
        background: linear-gradient(180deg, rgb(255, 255, 255), rgba(255, 255, 255, 0) 100%), rgb(244, 227, 233);
        &-container {
                width: remy(928);
                margin: 0 auto;
        }
        &__title {
                margin-bottom: remy(27);
                .h3 {
                        padding-left: remy(3);
                        line-height: 1.19;
                        letter-spacing: 0;
                        margin-bottom: remy(12);
                }
                p {
                        font-size: remy(18);
                        font-weight: 400;
                        line-height: remy(27);
                }
        }
        &__cards {
                padding-left: remy(97);
                display: flex;
                align-items: flex-start;
                .card__avatar {
                        padding-right: remy(17);
                }
                img {
                        flex-shrink: 0;
                        border-radius: 50%;
                        width: remy(59);
                        height: remy(57);
                }
        }
}
.card {
        flex-shrink: 0;
        width: remy(349);
        border-radius: remy(10);
        box-shadow: 1px 4px 8px 0px rgba(0, 0, 0, 0.18);
        background: rgb(255, 255, 255);
        padding: remy(35) remy(32) remy(38) remy(27);
        :nth-child(2){
                
        }
        .h5 {
                color: var(--accent);
                padding-bottom: remy(29);
        }
        &__icon {
                position: absolute;
        }
        &__text {
                position: relative;
                padding-left: remy(37);
                color: var(--sec2);
                font-size: remy(18);
                font-weight: 400;
                line-height: 1.78;
        }
        &__footer {
                padding-top: remy(20);
                padding-left: remy(39);
                display: flex;
                align-items: flex-end;
        }
        &__name {
                p {
                        font-size: remy(18);
                        font-weight: 600;
                }
                span {
                        font-size: remy(14);
                        font-weight: 400;
                        color: #969bab;
                }
        }
}
